.containerGrid {
    display: grid;
    grid-template-columns:  repeat(2, 1fr);
    max-width: 1400px;
    margin: 0 auto;
}
.itemFirst {
    background-color: #F1F1F2;
}

.itemFirstTitle {
    width: 433px;
    height: 206px;
    padding-left: 162px;
    padding-top: 154px;
    font-size: 56px;
    line-height: 69px;
}

.itemFirstText {
    width: 422px;
    height: 57px;
    padding-left: 162px;
    padding-top: 36px;
    font-size: 20px;
    line-height: 140.62%;
    color: #000000;
}

.itemFirstBtn {
    width: 200px;
    height: 60px;
    margin-left: 162px;
    background: #F7941E;
    border: none;
    border-radius: 5px;
    padding-left: 25px;
    padding-right: 24px;
    font-size: 18px;
    line-height: 22px;
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
}

.itemFourth {
    background-color: #F1F1F2;
}

.itemFourthTitle {
    font-size: 32px;
    line-height: 140.62%;
    margin-left: 103px;
}

.itemFourthText {
    font-size: 20px;
    line-height: 140.62%;
    text-align: justify;
    margin-left: 103px;
    margin-right: 166px;
}

.itemFourthBtn {
    border: 1px solid #F7941E;
    border-radius: 5px;
    margin-left: 103px;
    color: #F7941E;
    width: 200px;
    height: 60px;
    text-transform: uppercase;
}
