.wrapper {
    display: flex;
    width: 100%;
    padding-top: 2.8%;
    justify-content: space-between;
    align-items: baseline;
}

.logo {
    margin-left: 5.6%;
}
.menu {
    margin: 0;
    width: 400PX;
    padding-bottom: 40px;
}
ul {
    display: flex;
    flex-wrap: nowrap;
    gap: 31px;
}
li {
    align-items: center;
    list-style: none;
    width: fit-content;
    font-family: 'Roboto';
    font-size: 18px;
    line-height: 22px;
    color: #000000;
    justify-content: space-between;

}
a {
    text-decoration: none;
    color:black;
    width: 80px;

}

.btn {
    width: 150px;
    height: 44px;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
    color: #F7941E;
    background-color: #ffff;
    border: 1px solid #F7941E;
    border-radius: 5px;
    margin-right: 34px;
}



