.sectionFourthFirst {
    display: flex;
    margin: 50px 200px;
}
.fourthPartTitle {
    text-align: center;
    font-size: 32px;
    line-height: 38px;
}
.fourthPartQuote {
    text-align: center;
    font-size: 18px;
}
.wrapper {
    padding-left: 78px;
    text-align: left;
    display: flex;
    background: #000000;
    width: 1200px;
}
.sectionFourthImg {
    padding: 0;
    width: fit-content;
}
.sectionFourthFirstContainer {
    padding: 0;
    background: #000000;
    width: 66%;

}

.sectionFourthFirstContainerAuthor{
    margin-top: 63px;
    font-size: 14px;
    line-height: 140.62%;
    color: #FFFFFF;
}

.sectionFourthFirstContainerTitle {
    font-size: 48px;
    line-height: 58px;
    color: #FFFFFF;
    width: 495px;
}
.sectionFourthFirstContainerText {
    margin-top: 60px;
    font-size: 18px;
    line-height: 140.62%;
    color: #FFFFFF;
}
.sectionFourthFirstBox {
    display: flex;
    margin-left: 12%;
    gap: 10px;
    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
}




